import React from 'react';
import './Cards.css';
import CardItem from './CardItem';

function Cards() {
  return (
    <div className="cards">
      <h1>Projekty</h1>
      <div className="cards__container">
        <div className="cards__wrapper">
          <ul className="cards__items">
            <CardItem
              src="images/chocolate-hills.jpg"
              text="Chocolate Hills"
              label="Adventure"
              path="/services"
            />
            <CardItem
              src="images/czokoman.jpg"
              text="CzokoMan Quiz"
              label="Luxury"
              path="/services"
            />
          </ul>
          <ul className="cards__items">
            <CardItem
              src="images/portfolio.jpg"
              text="Moje portfolio"
              label="Mystery"
              path="/services"
            />
            <CardItem
              src="images/img-4.jpg"
              text=""
              label="Adventure"
              path="/products"
            />
            <CardItem
              src="images/img-8.jpg"
              text="Ride through the Sahara Desert on a guided camel tour"
              label="Adrenaline"
              path="/sign-up"
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cards;
